<template>
  <div class="update_name">
    <van-nav-bar
      class="topbar-container"
      title="修改昵称"
      left-text="取消"
      right-text="完成"
      @click-left="$emit(`close`)"
      @click-right="onConfirm"
    />
    <div class="name_wrap">
      <van-field
        rows="1"
        autosize
        label="当前昵称"
        type="textarea"
        maxlength="8"
        v-model="value"
        readonly
      />
      <van-field
        v-model.trim="newName"
        rows="1"
        autosize
        label="新昵称"
        type="textarea"
        maxlength="8"
        placeholder="请输入新昵称"
      />
    </div>
  </div>
</template>

<script>
import eventBus from '@/utils/eventBus.js';
import { updateUser } from '@/api/profile.js';

export default {
  name: 'UpdateName',
  props: {
    value: {
      required: true,
      type: [Number, String],
    },
  },
  data() {
    return {
      newName: '',
    };
  },
  methods: {
    // 确定提交修改用户昵称
    async onConfirm() {
      this.$toast.loading({
        message: '保存中',
        forbidClick: true,
      });
      try {
        const { data } = await updateUser({
          name: this.newName,
        });

        this.$toast.success('保存成功');
        this.$emit('input', data.data.name);
        eventBus.$emit('setUserName', data.data.name);
        this.$emit('close');
      } catch (e) {
        if (e && e.response && e.response.status == 409) {
          this.$toast.fail('已有的昵称,保存失败');
        }
      }
    },
  },
  created() {},

};
</script>

<style lang="less" scoped>
.topbar-container {
  /deep/ .van-nav-bar__content {
    .van-nav-bar__text {
      color: #fff;
    }

    .van-nav-bar__title {
      font-size: 14px;
    }
  }
}
.name_wrap {
  padding: 10px;
  /deep/ .van-field__body {
    border: 1px solid #eee;
    .van-field__control {
      text-indent: 8px;
    }
  }
}
</style>
